import startCase from 'lodash/startCase'
import { getEditorContentLength } from 'Shared/helpers/editor'
import { lengthLimit } from 'Shared/constants/Editor'

export default values => {
  const errors = {}

  if (!values.messageToEmployer) {
    errors.messageToEmployer = {
      key: 'errors.messageIsARequiredField',
      options: {}
    }
  }

  if (values['messageToEmployer'] && getEditorContentLength(values['messageToEmployer']) < 30) {
    errors['messageToEmployer'] = {
      key: 'errors.messageTooShort',
      options: {
        field: startCase('messageToEmployer'),
        chars: 30
      }
    }
  }

  if (values['messageToEmployer'] && getEditorContentLength(values['messageToEmployer']) > lengthLimit.messageToEmployer) {
    errors['messageToEmployer'] = {
      key: 'errors.charactersLength',
      options: {
        limit: lengthLimit.messageToEmployer
      }
    }
  }

  return errors
}



