import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import { hasSubmitSucceeded, submit, change } from 'redux-form'
import { formName as AvailabilityFormName } from 'Candidate/connectors/Forms/Availability/Availability'
import LanguageService from 'Shared/services/LanguageService'
import { ISOcodes } from 'Shared/constants/i18n'
import { flattenTranslations } from 'Shared/helpers/translations'
import { transformToArray, transformById } from 'Shared/helpers/redux'
import {
  isCandidateApproved,
  isCandidateRejected,
  isCandidateProfileIncomplete,
  isCandidateProfileComplete,
  isCandidateNotApproved
} from 'Shared/helpers/profile'
import { fetchJobOfferByIdIfNeeded, fetchSimilarJobOffersByIdIfNeeded } from 'Shared/ducks/jobOffers'
import { getApplicationIfAppliedIfNeeded } from 'Shared/ducks/jobOfferApplications'
import { openJobOfferDrawer, closeJobOfferDrawer } from 'Shared/ducks/ui'
import { fetchDepartments } from 'Shared/ducks/departments'
import { fetchCompanySize, fetchCurrenciesIfNeeded } from 'Shared/ducks/resources'
import { profileSteps } from 'Shared/ducks/user/candidate'
import { getFirstProfileUncompleteStep, isCandidateProfileApproved } from 'Shared/selectors/user'
import { hasCandidateAlreadyApplied } from 'Shared/selectors/jobOfferApplications'
import JobDetailsView from 'Candidate/views/JobDetailsView'
import { isStillAvailable } from 'Shared/helpers/date'
import { toggleJobOfferBookmark, fetchJobOffersBookmarksForCandidate } from 'Shared/ducks/jobOffersBookmarks'
import { getCurrencyCodeByJobOfferId } from 'Shared/selectors/resources'
import { getJobOfferById, isExternalJobOffer, isUsersOrganisationJobOffer } from 'Shared/selectors/jobOffers'
import { getOrganisation } from 'Shared/selectors/organisations'
import withCandidateUnbouncedPopup from 'Candidate/hoc/withCandidateUnbouncedPopup'
import {
  getEmployerMessageAmountLeft,
  getEmployerMessageMaxAmount,
  hasOlivPlusMembership
} from 'Shared/selectors/membership'
import { fetchOlivPlusMembershipPlan } from 'Shared/ducks/membership'
import { formName as ApplyFormName } from 'Candidate/connectors/Forms/JobOffer/JobOfferApplyFormConnector'
import { getChannelByJobOfferId } from 'Chat/selectors/channels'

const mapStateToProps = (state, ownProps) => {
  const { profileCompletionStatus, availableTo } = state.user.details
  const jobOffer = getJobOfferById(state, ownProps.match.params.id)

  return ({
    isCandidateStillAvailable: isStillAvailable(availableTo),
    userRole: state.user.role,
    jobOffer: jobOffer,
    organisation: getOrganisation(state, jobOffer.organisationId),
    similarJobOffers: state.jobOffers.similarJobOffers[ownProps.match.params.id] || [],
    companySize: transformById(flattenTranslations(
      transformToArray(state.resources.companySize.entries),
      ISOcodes[LanguageService.userLanguage]
    ), 'value'),
    departments: transformById(flattenTranslations(
      transformToArray(state.departments.entries),
      ISOcodes[LanguageService.userLanguage]
    )),
    isCandidateApproved: isCandidateApproved(profileCompletionStatus),
    isCandidateRejected: isCandidateRejected(profileCompletionStatus),
    isCandidateProfileComplete: isCandidateProfileComplete(profileCompletionStatus),
    isCandidateProfileIncomplete: isCandidateProfileIncomplete(profileCompletionStatus),
    isCandidateNotApproved: isCandidateNotApproved(profileCompletionStatus),
    candidateFirstIncompleteStep: getFirstProfileUncompleteStep(state),
    jobOfferDrawerVisible: state.ui.jobOfferDrawerVisible,
    hasCandidateAlreadyApplied: hasCandidateAlreadyApplied(ownProps.match.params.id)(state),
    hasAvailabilityUpdateFormSucceeded: hasSubmitSucceeded(AvailabilityFormName)(state),
    candidateId: state.user.details.id,
    jobOffersBookmarks: state.jobOffersBookmarks.entries,
    isAlreadyApplied: hasCandidateAlreadyApplied(ownProps.match.params.id)(state),
    selectedCurrency: getCurrencyCodeByJobOfferId(state, ownProps.match.params.id),
    isUsersOrganisationJobOffer: isUsersOrganisationJobOffer(state, ownProps.match.params.id),
    hasMembership: hasOlivPlusMembership(state),
    employerMessageAmountLeft: getEmployerMessageAmountLeft(state),
    employerMessageMaxAmount: getEmployerMessageMaxAmount(state),
    isApproved: isCandidateProfileApproved(state),
    channel: getChannelByJobOfferId(state, jobOffer.id),
    isExternalJobOffer: isExternalJobOffer(state, jobOffer.id),
    user: state.user.details
  })
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  openDrawer: () => dispatch(openJobOfferDrawer()),
  closeDrawer: () => dispatch(closeJobOfferDrawer()),
  getJobOffer: async () => {
    try {
      await dispatch(fetchJobOfferByIdIfNeeded(ownProps.match.params.id))
    } catch (e) {
      return ownProps.history.push(`./${ownProps.match.params.id}/not-found`)
    }
  },
  getDepartments: () => dispatch(fetchDepartments()),
  fetchCompanySize: () => dispatch(fetchCompanySize()),
  profileSteps: () => dispatch(profileSteps()),
  fetchCurrencies: () => dispatch(fetchCurrenciesIfNeeded()),
  getApplicationIfApplied: () => dispatch(getApplicationIfAppliedIfNeeded(ownProps.match.params.id)),
  fetchSimilarJobOffers: () => dispatch(fetchSimilarJobOffersByIdIfNeeded(ownProps.match.params.id)),
  fetchJobOffersBookmarksForCandidate: (candidateId) => dispatch(fetchJobOffersBookmarksForCandidate(candidateId)),
  toggleJobOfferBookmark: jobOfferId => dispatch(toggleJobOfferBookmark(jobOfferId)),
  fetchOlivPlusMembershipPlan: () => dispatch(fetchOlivPlusMembershipPlan()),
  submitForm: () => dispatch(submit(ApplyFormName)),
  submitApplyFormWithBoost: async () => {
    await dispatch(change(ApplyFormName, 'shouldBoostApplication', true))
    return dispatch(submit(ApplyFormName))
  }
})

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps),
  withCandidateUnbouncedPopup
)(JobDetailsView)



