import { connect } from 'react-redux'
import { reduxForm } from 'redux-form'
import AvailabilityForm from 'Candidate/forms/Availability/index'
import validate from './AvailabilityValidator'
import { updateCandidate } from 'Shared/ducks/user/candidate'

export const formName = 'Availability'

const Form = reduxForm({
  form: formName,
  validate,
  onSubmit: (values, dispatch) => dispatch(updateCandidate(values))
})(AvailabilityForm)

const mapStateToProps = state => ({
  initialValues: {
    availableFrom: state.user.details.availableFrom,
    availableTo: state.user.details.availableTo
  }
})

export default connect(mapStateToProps)(Form)



