import startCase from 'lodash/startCase'
import { getEditorContentLength } from 'Shared/helpers/editor'
import { lengthLimit } from 'Shared/constants/Editor'

const validate = values => {
  const errors = {}

  const validateCoverLetter = ({ coverLetterRequired }) => coverLetterRequired
    ? { coverLetter: 'coverLetter' }
    : {}

  const requiredFields = {
    ...validateCoverLetter(values)
  }

  const [min, max] = lengthLimit.coverLetter
  const { coverLetter } = requiredFields

  Object.values(requiredFields).forEach(field => {
    if (!values[field]) {
      errors[field] = {
        key: 'errors.requiredField',
        options: {
          field: startCase(field)
        }
      }
    }
  })

  if (!values['cvId']) {
    errors['cvId'] = {
      key: 'errors.requiredField',
      options: {
        field: 'CV'
      }
    }
  }

  if (getEditorContentLength(values['coverLetter']) && getEditorContentLength(values['coverLetter']) < min) {
    errors['coverLetter'] = {
      key: 'errors.coverLetterTooShort',
      options: {
        field: startCase(coverLetter),
        chars: startCase(min.toString())
      }
    }
  }

  if (getEditorContentLength(values['coverLetter']) && getEditorContentLength(values['coverLetter']) > max) {
    errors['coverLetter'] = {
      key: 'errors.coverLetterTooLong',
      options: {
        field: startCase(coverLetter),
        chars: startCase(max.toString())
      }
    }
  }

  return errors
}

export default validate



