import React, { PureComponent } from 'react'
import { Helmet } from 'react-helmet'
import PropTypes from 'prop-types'
import { Link, NavLink, withRouter } from 'react-router-dom'
import scriptLoader from 'react-async-script-loader'
import { Trans } from 'react-i18next'
import { t } from 'i18next'
import notie from 'notie'
import * as R from 'ramda'
import { PublicRoutes, StaticRoutes } from 'Shared/constants/Routes'
import { formats } from 'Shared/constants/Date'
import { Roles } from 'Shared/constants/User'
import withTranslations from 'Shared/hoc/withTranslations'
import {
  ASAP_LABEL,
  DEFAULT_JOB_OFFER_LOCATION_ZOOM,
  JobTypes,
  Statuses,
  AvailabilityCommitmentTypes,
  DurationTypesLabels
} from 'Shared/constants/Job'
import LanguageService from 'Shared/services/LanguageService'
import { ISOcodes } from 'Shared/constants/i18n'
import { showPropOr } from 'Shared/helpers/responseData'
import { formatDate, getDateFromNow } from 'Shared/helpers/date'
import Container from 'Shared/components/Container'
import Divider from 'Shared/components/FormElements/Divider'
import LocationMap from 'Shared/components/LocationMap'
import { parseLocationCoords } from 'Shared/helpers/Location'
import { PrimaryButton } from 'Shared/components/Button'
import Drawer from 'Shared/components/Drawer/Drawer'
import JobOfferTag from 'Shared/components/JobOfferTag'
import { getUserTranslationsOrDefault } from 'Shared/helpers/translations'
import { getCountryNameByISOCode } from 'Shared/helpers/Country'
import JobOfferApplyFormConnector from 'Candidate/connectors/Forms/JobOffer/JobOfferApplyFormConnector'
import BackButton from 'Shared/components/BackButton/BackButton'
import Subtitle from 'Shared/components/Subtitle'
import { StaticTag } from 'Shared/components/StaticTag'
import CompanyDetailsSidebarConnector from 'Shared/connectors/Components/CompanyDetailsSidebarConnector'
import { ConfirmModal } from 'Shared/components/DeleteModal'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import { NOT_AVAILABLE } from 'Shared/constants/Placeholders'
import Notification from 'Shared/components/Notification'
import RejectMessageModal from 'Candidate/components/RejectMessageModal'
import { generateJobOfferURL, cleanseString, dasherize } from 'Shared/helpers/string'
import ShareDropdown from 'Shared/components/ShareDropdown'
import withLoader from 'Shared/hoc/withLoader/withLoader'
import AvailabilityModalConnector from 'Candidate/connectors/AvailabilityModalConnector'
import { JobDetailsLoader } from 'Shared/components/Main/MainLoaders'
import JobOfferCardAlgoliaConnector from 'Candidate/connectors/JobOfferCardAlgolia'
import BookmarkButton from 'Shared/components/BookmarkButton'
import { generateCustomCareerUrlWithUtms } from 'Shared/helpers/query'
import cx from 'classnames'
import AdvertCampaignConnector from 'Shared/connectors/Components/AdvertCampaign/AdvertCampaignConnector'
import { BANNER_TYPES } from 'Shared/constants/advertCampaign'
import './JobDetailsView.scss'
import MessageToEmployerButton from 'Candidate/components/MessageToEmployerButton/MessageToEmployerButton'
import OlivPlusSmartPopUpConnector from 'Candidate/connectors/OlivPlusSmartPopUpConnector/OlivPlusSmartPopUpConnector'
import { isNotNilOrEmpty } from 'Shared/helpers/Ramda'
import CareerproBanner from 'Shared/components/CareerproBanner/CareerproBanner'
import { apiEmailCareerPro } from 'Shared/services/api/useApiEmailCareerPro'

class JobDetailsView extends PureComponent {
  state = {
    isApproveNoticeModalOpen: false,
    isExpireNotificationVisible: false,
    isRejectMessageModalOpen: false,
    isCandidateProfileCompletionModalOpen: false,
    isAvailabilityModalOpen: false,
    ready: false,
    isComponentReady: false,
    isMessageModalOpen: false
  }

  async componentDidMount () {
    if (this.props.userRole === Roles.candidate.name) {
      await this.props.getApplicationIfApplied()
      this.props.fetchJobOffersBookmarksForCandidate(this.props.candidateId)
      this.props.fetchOlivPlusMembershipPlan()
    }
    await this.props.fetchCurrencies()
    await this.props.getDepartments()
    await this.props.fetchCompanySize()
    await this.props.getJobOffer()

    if (this.props.jobOffer.status === Statuses.inactive && !this.props.isUsersOrganisationJobOffer) {
      this.setState({ isExpireNotificationVisible: true })
    }

    if (this.props.userRole === Roles.candidate.name) {
      this.props.profileSteps()
    }
    this.props.finishLoading(this.props.jobOffer)
    this.setComponentReady()

    this.props.fetchSimilarJobOffers()
  }

  setComponentReady = () => this.setState({ isComponentReady: true })

  componentDidUpdate (prevProps) {
    if (this.props.hasAvailabilityUpdateFormSucceeded && prevProps.hasAvailabilityUpdateFormSucceeded !== this.props.hasAvailabilityUpdateFormSucceeded) {
      this.props.openDrawer()
    }
  }

  componentWillReceiveProps ({ isScriptLoaded, isScriptLoadSucceed }) {
    if (isScriptLoaded && isScriptLoadSucceed) {
      this.setState({ ready: true })
    }
  }

  componentWillUnmount () {
    this.props.closeDrawer()
  }

  redirectToSignup = () => this.props.history.push({
    pathname: StaticRoutes.candidate.signUp,
    state: { from: this.props.location }
  })

  handleJobApplication = () => {
    apiEmailCareerPro(this.props.user, 'has_applied_more_opportunities')
    switch (true) {
      case this.props.userRole !== Roles.candidate.name:
        return this.redirectToSignup()
      case this.props.isCandidateApproved || this.props.isCandidateProfileComplete:
        return this.props.openDrawer()
      case this.props.isCandidateNotApproved:
        return this.openApproveNoticeModal()
      case this.props.isCandidateProfileIncomplete:
        return this.openCandidateProfileCompletionModal()
    }
  }

  openApproveNoticeModal = () => this.setState({ isApproveNoticeModalOpen: true })
  closeApproveNoticeModal = () => this.setState({ isApproveNoticeModalOpen: false })

  openRejectMessageModal = () => this.setState({ isRejectMessageModalOpen: true })
  closeRejectMessageModal = () => this.setState({ isRejectMessageModalOpen: false })

  closeExpireNotificationModal = () => this.setState({ isExpireNotificationVisible: false })
  confirmExpireNotificationModal = () => this.props.history.push('/jobs')

  openCandidateProfileCompletionModal = () => this.setState({ isCandidateProfileCompletionModalOpen: true })
  closeCandidateProfileCompletionModal = () => this.setState({ isCandidateProfileCompletionModalOpen: false })
  confirmCandidateProfileCompletionModal = () => this.props.history.push(`/candidate/profile/complete/${this.props.candidateFirstIncompleteStep}`)

  openAvailabilityModal = () => this.setState({ isAvailabilityModalOpen: true })
  closeAvailabilityModal = () => this.setState({ isAvailabilityModalOpen: false })

  goBack = () => {
    const landingPagePaths = ['/', '', '/employers']
    const previousPath = R.pathOr(null, ['history', 'location', 'state', 'from'], this.props)
    const isFromLandingPage = landingPagePaths.includes(previousPath)
    const jobsListPath = PublicRoutes.jobs

    switch (true) {
      case isFromLandingPage:
        return this.props.history.push(jobsListPath)
      case R.not(R.isNil(previousPath)):
        return this.props.history.push(previousPath)
      default:
        return this.props.history.push(jobsListPath)
    }
  }

  get notificationMessage () {
    return (
      <Trans i18nKey='rejectedCandidateBox.cannotApply' parent='span' className='job-offer__rejected-candidate-box'>
        <span>You cannot apply for jobs as your profile has been rejected. </span>
        <span className='view-message' onClick={this.openRejectMessageModal}>View message</span>
      </Trans>
    )
  }

  redirectToFeaturedJobOffer = url => () => window.open(url)

  get applyButton () {
    const { jobOffer, isCandidateRejected, t } = this.props

    switch (true) {
      case isCandidateRejected:
        return (
          <Notification
            messageText={this.notificationMessage}
            iconClassName='icon-im-info-outline'
            error
          />)
      case process.env.REACT_APP_FEATURED_JOB_OFFER === jobOffer.id:
        return (
          <div key={`${jobOffer.id}-apply-button`} className='columns job-offer-apply-section'>
            <div className='column col-6 col-sm-12'>
              <PrimaryButton
                onClick={this.redirectToFeaturedJobOffer('https://employers.oliv.com/emaar/', '_blank')}>{t('jobOffer.jobDetails.applyForThisJob')}</PrimaryButton>
            </div>
          </div>
        )
      case this.props.userRole === Roles.guest.name && jobOffer.status === Statuses.active:
        return (
          <div key={`${jobOffer.id}-apply-button`} className='columns job-offer-apply-section'>
            <div className='column col-6 col-sm-12'>
              <PrimaryButton onClick={this.handleJobApplication}>
                {t('jobOffer.jobDetails.applyNow')}
              </PrimaryButton>
            </div>
          </div>
        )
      case this.isCandidate && R.not(this.props.isCandidateStillAvailable) && this.props.isCandidateApproved:
        return (
          <div key={`${jobOffer.id}-apply-button`} className='columns job-offer-apply-section'>
            <div className='column col-6 col-sm-12'>
              <PrimaryButton onClick={this.openAvailabilityModal}>{t('jobOffer.jobDetails.applyForThisJob')}</PrimaryButton>
            </div>
          </div>
        )
      case this.isCandidate && jobOffer.status === Statuses.active && !this.props.hasCandidateAlreadyApplied:
        return (
          <div key={`${jobOffer.id}-apply-button`} className='columns job-offer-apply-section'>
            <div className='column col-6 col-sm-12'>
              <PrimaryButton onClick={this.handleJobApplication}>
                {
                  R.not(R.isNil(jobOffer.customCareerSiteUrl))
                    ? t('jobOffer.jobDetailsDrawer.externalLink.applyBtn')
                    : t('jobOffer.jobDetails.applyForThisJob')
                }
              </PrimaryButton>
            </div>
          </div>
        )
      case !R.isNil(jobOffer.customCareerSiteUrl) && R.not(this.isEmployer) && jobOffer.status === Statuses.active && this.props.hasCandidateAlreadyApplied:
        return (
          <div key={`${jobOffer.id}-apply-button`} className='columns job-offer-apply-section'>
            <div className='column col-6 col-sm-12'>
              <PrimaryButton
                onClick={this.redirectToFeaturedJobOffer(generateCustomCareerUrlWithUtms(jobOffer.customCareerSiteUrl))}>{t('jobOffer.jobDetailsDrawer.externalLink.applyBtn')}</PrimaryButton>
            </div>
          </div>
        )
      case this.isCandidate && this.props.hasCandidateAlreadyApplied:
        return (
          <div className='columns job-offer-apply-section'>
            <div className='column col-8 col-sm-12'>
              <PrimaryButton disabled withIcon iconClassName='icon-im-done'>{t('jobOffer.jobDetails.alreadyApplied')}</PrimaryButton>
            </div>
          </div>
        )
    }
  }

  get drawer () {
    const { jobOffer, t } = this.props

    return this.isCandidate && this.props.jobOfferDrawerVisible && (
      <Drawer
        key={`${jobOffer.id}-apply-drawer`}
        title={t('jobOffer.jobDetailsDrawer.applyForAJob')}
        isOpen={this.props.jobOfferDrawerVisible}
        onClose={this.props.closeDrawer}
      >
        {/* Careerpro Ads Banner */}
        <CareerproBanner
          name='jobNotify'
          des='Ace your application process with CareerPro resume & cover letter writing service!'
          className='jobNotify'
          arrowSubmitButton
        />
        <JobOfferApplyFormConnector jobOffer={jobOffer} closeOnSubmit={this.props.closeDrawer} />
      </Drawer>
    )
  }

  renderDepartment = () => {
    const { jobOffer, departments } = this.props

    return R.pathOr('', [jobOffer.departmentId, 'description'], departments)
  }

  renderStartDate = () => {
    const { startDate, startDateAsap } = this.props.jobOffer

    switch (true) {
      case startDateAsap:
        return ASAP_LABEL
      case R.not(R.isNil(startDate)):
        return formatDate(startDate, formats.dayYear)
      default:
        return NOT_AVAILABLE
    }
  }

  renderEmploymentType = () => {
    const { t, jobOffer: { jobType, permanentJobOpportunity } } = this.props

    if (JobTypes()[jobType]) {
      return (
        <div>
          <div>{JobTypes()[jobType]}</div>
          { jobType === JobTypes().mirror.internship && permanentJobOpportunity &&
          <div className='job-offer-details__sub-header'>{t('employer.jobOffers.internshipLeadingToPermanent')}</div>
          }
        </div>
      )
    }
    return NOT_AVAILABLE
  }

  get pageTitle () {
    const { jobOffer, organisation } = this.props
    const title = jobOffer.title || ''
    const city = jobOffer.city || ''
    const organisationName = R.propOr('', 'name', organisation)

    const formattedTitle = t('seo.jobDetails.title', { title, organisationName, city })

    return cleanseString(formattedTitle)
  }

  isEmployer = this.props.userRole === Roles.employer.name
  isCandidate = this.props.userRole === Roles.candidate.name

  renderRemuneration = () => {
    const { t, jobOffer, selectedCurrency } = this.props
    if (jobOffer.remunerationType) {
      return jobOffer.remunerationType === 'paid' ? <span>{jobOffer.remuneration ? `${jobOffer.remuneration} ${selectedCurrency}` : ''}</span>
        : <span>{t(`jobOffer.jobDetails.${jobOffer.remunerationType}`)}</span>
    }
    return NOT_AVAILABLE
  }

  renderJobSkills = () => {
    const { skills } = this.props.jobOffer
    return (
      skills.map(skill => {
        return <StaticTag key={skill.id} name={getUserTranslationsOrDefault(skill, ISOcodes[LanguageService.userLanguage]).name} />
      })
    )
  }

  renderRemunerationString = () => {
    const { t, jobOffer } = this.props

    return jobOffer.remunerationType === 'paid'
      ? jobOffer.remuneration
      : t(`jobOffer.jobDetails.${jobOffer.remunerationType}`)
  }

  renderSkillString = () => {
    const { skills } = this.props.jobOffer
    return (
      skills
        ? skills
          .map(skill => getUserTranslationsOrDefault(skill, ISOcodes[LanguageService.userLanguage], []).name)
          .join(', ')
        : NOT_AVAILABLE
    )
  }

  renderDuration = () => {
    const { jobOffer: {duration} } = this.props
    return DurationTypesLabels()[duration] ? DurationTypesLabels()[duration] : NOT_AVAILABLE
  }

  toggleBookmark = async () => {
    try {
      const data = await this.props.toggleJobOfferBookmark(this.props.jobOffer.id)
      const alertText = R.ifElse(
        R.isEmpty,
        () => t('alerts.removeJobOfferFromBookmarks', { jobTitle: this.props.jobOffer.title }),
        () => t('alerts.addJobOfferToBookmarks', { jobTitle: this.props.jobOffer.title })
      )

      return notie.alert({
        type: 'success',
        text: alertText(data)
      })
    } catch (e) {
      return notie.alert({
        type: 'error',
        text: t('alerts.addJobOfferToBookmarksFailed')
      })
    }
  }

  onViewChatChannelClick = channelSid => () => {
    this.props.history.push(`/candidate/chat/${channelSid}`)
  }

  messageButton = () => {
    const { channel, jobOffer, isExternalJobOffer } = this.props
    const sid = R.propOr('', 'sid')(channel)
    const channelExists = isNotNilOrEmpty(channel)

    switch (true) {
      case channelExists:
        return <MessageToEmployerButton onClick={this.onViewChatChannelClick(sid)} />
      case !channelExists && !isExternalJobOffer:
        return <OlivPlusSmartPopUpConnector
          triggerButton={<MessageToEmployerButton />}
          type={'cta'}
          service={'message'}
          jobOfferIdProps={jobOffer.id}
        />
      default:
        return null
    }
  }

  render () {
    const { t, jobOffer, isAlreadyApplied, jobOffersBookmarks, userRole, similarJobOffers, organisation, isApproved, hasMembership } = this.props
    const { isAvailabilityModalOpen, isCandidateProfileCompletionModalOpen, isRejectMessageModalOpen, ready,
      isApproveNoticeModalOpen, isExpireNotificationVisible } = this.state
    const { title, description, requirements, updatedAt, expiresAt, jobType, publishedAt, isFeatured,
      city, id, commitment, country, skills, address, locationCoords, scrapingSource } = jobOffer

    const organisationName = R.propOr('', 'name', organisation)
    const organisationLogoUrl = R.propOr('', 'logoUrl', organisation)

    const makeDescription = () =>
      (description ? '<h1><b>About this role:</b></h1><br/><br/>' + description + '<br/><br/>' : '') +
      (requirements ? '<h1><b>Requirements to apply:</b></h1><br/><br/>' + requirements + '<br/><br/>' : '') +
      (organisation ? '<h1><b>Company description</b></h1><br/><br/>' + showPropOr('description', '', organisation) : '')

    const isApprovedWithoutMembership = isApproved && !hasMembership

    const detailsContainerClass = cx({
      'job-offer-details-container': true,
      'job-offer-details-lesser-margin': isApproved && !hasMembership
    })

    return (
      <div className='job-details__container parsing--hidden' style={{ visibility: this.state.isComponentReady ? 'visible' : 'hidden' }}>
        <div className='job-offer-main-header'>
          <Container>
            <Helmet>
              <title>{this.pageTitle}</title>
              <meta name='description' content={`${description ? description.slice(0, 200).replace(/(<([^>]+)>)/ig, '') : NOT_AVAILABLE}...`} />
              <meta property='og:title' content={this.pageTitle} />
              <meta property='og:type' content='website' />
              <meta property='og:image' content={organisationLogoUrl} />
              <meta property='og:image:width' content='200' />
              <meta property='og:image:height' content='200' />
              <meta property='og:url' content={window.location.href} />
              <meta property='og:description' content={`${description ? description.slice(0, 200).replace(/(<([^>]+)>)/ig, '') : NOT_AVAILABLE}...`} />
              {!scrapingSource && <script type='application/ld+json'>{`
                  {
                    "@context": "http://schema.org/",
                    "@type": "JobPosting",
                    "title": "${title}",
                    "description": "${makeDescription()}",
                    "experienceRequirements": "${requirements}",
                    "datePosted": "${updatedAt}",
                    "validThrough": "${expiresAt}",
                    "employmentType": "${JobTypes()[jobType]}",
                    "industry": "${this.renderDepartment()}",
                    "skills": "${this.renderSkillString()}",
                    "hiringOrganization": {
                      "@type": "Organization",
                      "name": "${organisationName}",
                      "sameAs": "${showPropOr('website', NOT_AVAILABLE, organisation)}",
                      "logo": "${organisationLogoUrl}",
                      "location": {
                        "@type": "Place",
                        "address": {
                          "@type": "PostalAddress",
                          "addressLocality": "${showPropOr('city', NOT_AVAILABLE, organisation)}",
                          "addressCountry": "${showPropOr('country', NOT_AVAILABLE, organisation)}",
                          "streetAddress": "${showPropOr('address', NOT_AVAILABLE, organisation)}"
                        }
                      }
                    },
                    "jobLocation": {
                      "@type": "Place",
                      "address": {
                        "@type": "PostalAddress",
                        "addressLocality": "${showPropOr('city', NOT_AVAILABLE, jobOffer)}",
                        "addressCountry": "${showPropOr('country', NOT_AVAILABLE, jobOffer)}",
                        "streetAddress": "${showPropOr('address', NOT_AVAILABLE, jobOffer)}",
                        "addressRegion": "${showPropOr('country', NOT_AVAILABLE, jobOffer)}",
                        "postalCode": "NA"
                      }
                    },
                    "baseSalary": {
                      "@type": "MonetaryAmount",
                      "currency": "AED",
                      "value": "${this.renderRemunerationString()}"
                    }
                  }
              `}</script>}
            </Helmet>
            <div className='columns'>
              <div className='column col-sm-12'>
                <div className='job-offer-main-header__job-tags'>
                  {publishedAt &&
                  <Subtitle
                    className='job-offer-main-header__postdate'
                    text={`${t('jobOffer.jobDetails.postedOn')} ${getDateFromNow(publishedAt)}`}
                  />
                  }
                  <div className='job-offer-header-tag'><JobOfferTag isFeatured={isFeatured} /></div>
                  <div className='job-offer-header__share--mobile'>
                    { userRole === Roles.candidate.name &&
                      <div className='job-offer-header__bookmark--mobile'>
                        <BookmarkButton
                          disabled={isAlreadyApplied}
                          withTooltip={isAlreadyApplied}
                          isActive={R.not(R.isNil(jobOffersBookmarks[id]))}
                          onClick={this.toggleBookmark}
                        />
                      </div>
                    }
                    <ShareDropdown
                      shareUrl={generateJobOfferURL(jobOffer)}
                      title={`${title} at ${organisationName} in ${city}`}
                    />
                  </div>
                </div>
                <div className='columns job-offer-main-header__job-title'>
                  <h1 className='column col-6 col-md-12 job-offer-main-header__job-title--text'>{title}</h1>
                  <div className='column col-6 job-offer-main-header__buttons-container'>
                    { userRole === Roles.candidate.name &&
                      <div className='job-offer-header__bookmark--desktop'>
                        <BookmarkButton
                          withText
                          disabled={isAlreadyApplied}
                          withTooltip={isAlreadyApplied}
                          isActive={R.not(R.isNil(jobOffersBookmarks[id]))}
                          onClick={this.toggleBookmark}
                        />
                      </div>
                    }
                    <div className='job-offer-header__share--desktop'>
                      <ShareDropdown
                        withText
                        shareUrl={generateJobOfferURL(jobOffer)}
                        title={`${title} at ${organisationName} in ${city}`}
                      />
                    </div>
                    {isApproved && this.messageButton()}
                  </div>
                </div>
                <Link className='job-offer-main-header__organisation-name' to={`/companies/${dasherize(organisationName)}/${organisation.id}`}>{organisationName}
                  <span>{city}, {country && getCountryNameByISOCode(country)} </span>
                </Link>
                <div className='job-offer-details__job-category'>
                  {this.renderDepartment()}
                </div>
                <div className='hide-sm'><BackButton button onClick={this.goBack} name={t('common.goBackToOffers')} /></div>
              </div>
              <div className='show-sm job-offer-header__back-button-mobile'><BackButton button onClick={this.goBack} name={t('common.goBackToOffers')} /></div>
            </div>
          </Container>
        </div>
        <Container>
          {isApprovedWithoutMembership && <Notification
            messageText={<span>
              {t('olivPlus.boostYourApplication')}
              &nbsp;<NavLink className='oliv-plus__alert-link' to={'/candidate/oliv-plus'}>{t('olivPlus.learMore')}</NavLink>
            </span>}
            iconClassName='icon-im-star-circle'
            warning
          />}
          <div className='columns'>
            <section className='column col-8 col-md-12 job-offer-main-content'>
              <div className={detailsContainerClass}>
                <div className='column job-offer-details-info'>
                  <div className='col-4 col-sm-6 job-offer-details__employment-type'>
                    <div className='job-offer-details__header'>{t('jobOffer.jobDetails.employmentType')}</div>
                    {this.renderEmploymentType()}
                  </div>
                  <div className='col-4 col-sm-6 job-offer-details__availability'>
                    <div className='job-offer-details__header'>{t('jobOffer.jobDetails.availability')}</div>
                    {AvailabilityCommitmentTypes()[commitment] ? AvailabilityCommitmentTypes()[commitment] : NOT_AVAILABLE}
                  </div>
                  <div className='col-4 col-sm-6 job-offer-details__duration'>
                    <div className='job-offer-details__header'>{t('jobOffer.jobDetails.duration')}</div>
                    {this.renderDuration()}
                  </div>
                  <div className='col-4 col-sm-6 job-offer-details__compensation'>
                    <div className='job-offer-details__header'>{t('jobOffer.jobDetails.compensation')}</div>
                    {this.renderRemuneration()}
                  </div>
                  <div className='col-4 col-sm-6 job-offer-details__start'>
                    <div className='job-offer-details__header'>{t('jobOffer.jobDetails.startDate')}</div>
                    {this.renderStartDate()}
                  </div>
                  <div className='col-4 col-sm-6 job-offer-details__deadline'>
                    <div className='job-offer-details__header'>{t('jobOffer.jobDetails.deadline')}</div>
                    {formatDate(expiresAt, formats.dayYear) || NOT_AVAILABLE}
                  </div>
                  <Divider />
                </div>
                <div className='job-offer-details__about'>
                  <h3>{t('jobOffer.jobDetails.aboutThisRole')}</h3>
                  <div>{description ? parseHtmlWithoutSpan(description) : NOT_AVAILABLE}</div>
                  <Divider />
                </div>
                {requirements && <div className='job-offer-details__requirements'>
                  <h3>{t('jobOffer.jobDetails.requirementsToApply')}</h3>
                  <div>{parseHtmlWithoutSpan(requirements)}</div>
                  <Divider />
                </div>
                }
                {
                  (skills && skills.length > 0)
                    ? <div className='job-offer-details__skills'>
                      <h3>{t('jobOffer.jobDetails.jobSkills')}</h3>
                      {this.renderJobSkills()}
                      <Divider />
                    </div>
                    : ''
                }
                {
                  ( address ) &&
                  <div className='job-offer-details__location'>
                    <h3>{t('jobOffer.jobDetails.jobLocation')}</h3>
                    <div className='job-offer-details__location-container'>
                      <div className='job-offer-details__location--address'>{address}</div>
                      {/*
                        !R.isNil(locationCoords) && ready &&
                        <LocationMap
                          defaultZoom={DEFAULT_JOB_OFFER_LOCATION_ZOOM}
                          location={parseLocationCoords(locationCoords)}
                          loadingElement={<div style={{height: `100%`}} />}
                          containerElement={<div style={{height: `200px`, marginTop: '22px', marginBottom: '48px'}} />}
                          mapElement={<div style={{height: `100%`}} />}
                        />
                      */}
                    </div>
                  </div>
                }
                {this.applyButton}
                {this.drawer}
              </div>
            </section>
            {R.not(R.isEmpty(organisation)) && <section className='column col-4 col-md-12 job-offer__company--sidebar'>
              <CompanyDetailsSidebarConnector jobOfferId={id} lesserMargin={isApprovedWithoutMembership} />
              <AdvertCampaignConnector type={BANNER_TYPES.JOB_DETAILS_BANNER} />
            </section>}
          </div>
        </Container>
        <div>
          <Container>
            <h3>{t('jobOffer.similarJobs')}</h3>
          </Container>
          <Container className='job-offer-details__similar-jobs--container'>
            {similarJobOffers.map((job, index) => <JobOfferCardAlgoliaConnector key={job.id} hit={job} hitNumber={index} />)}
          </Container>
        </div>

        <ConfirmModal
          title={t('modals.notApprovedUserNotice.title')}
          content={t('modals.notApprovedUserNotice.description')}
          handleConfirm={this.closeApproveNoticeModal}
          isModalVisible={isApproveNoticeModalOpen}
          hideConfirmModal={this.closeApproveNoticeModal}
        />
        <ConfirmModal
          title={t('modals.jobOfferExpired.title')}
          content={t('modals.jobOfferExpired.description')}
          handleConfirm={this.confirmExpireNotificationModal}
          isModalVisible={isExpireNotificationVisible}
          hideConfirmModal={this.closeExpireNotificationModal}
        />
        <RejectMessageModal
          closeMessageModal={this.closeRejectMessageModal}
          isMessageModalOpen={isRejectMessageModalOpen}
        />
        {
          this.isCandidate &&
          <ConfirmModal
            title={t('modals.applyWhenProfileNotCompleted.title')}
            content={t('modals.applyWhenProfileNotCompleted.description')}
            handleConfirm={this.confirmCandidateProfileCompletionModal}
            isModalVisible={isCandidateProfileCompletionModalOpen}
            hideConfirmModal={this.closeCandidateProfileCompletionModal}
            submitText={'Complete profile'}
          />
        }
        <AvailabilityModalConnector
          show={isAvailabilityModalOpen}
          onClose={this.closeAvailabilityModal}
        />
      </div>
    )
  }
}

JobDetailsView.defaultProps = {
  jobOffer: {
    id: '',
    organisationId: '',
    employerId: '',
    departmentId: '',
    title: '',
    description: '',
    jobType: '',
    locationCoords: '',
    country: '',
    city: '',
    address: '',
    coverLetterRequired: false,
    preferredGender: '',
    preferredVideoCv: false,
    preferredExperience: false,
    duration: '',
    startDate: '',
    startDateAsap: false,
    commitment: '',
    remunerationType: '',
    remuneration: 0,
    requirements: '',
    numberOfVacancies: 0,
    status: '',
    createdAt: '',
    updatedAt: '',
    expiresAt: '',
    googlePlacesId: '',
    skills: []
  },
  organisation: {}
}

JobDetailsView.propTypes = {
  userRole: PropTypes.string.isRequired,
  isCandidateStillAvailable: PropTypes.bool.isRequired,
  jobOfferDrawerVisible: PropTypes.bool.isRequired,
  openDrawer: PropTypes.func.isRequired,
  closeDrawer: PropTypes.func.isRequired,
  getJobOffer: PropTypes.func.isRequired,
  jobOffer: PropTypes.shape({
    id: PropTypes.string,
    organisationId: PropTypes.string,
    employerId: PropTypes.string,
    departmentId: PropTypes.string,
    title: PropTypes.string,
    description: PropTypes.string,
    jobType: PropTypes.string,
    locationCoords: PropTypes.string,
    country: PropTypes.string,
    city: PropTypes.string,
    address: PropTypes.string,
    coverLetterRequired: PropTypes.bool,
    preferredGender: PropTypes.string,
    preferredVideoCv: PropTypes.bool,
    preferredExperience: PropTypes.bool,
    duration: PropTypes.string,
    startDate: PropTypes.string,
    startDateAsap: PropTypes.bool,
    commitment: PropTypes.string,
    remunerationType: PropTypes.string,
    remuneration: PropTypes.number,
    requirements: PropTypes.string,
    numberOfVacancies: PropTypes.number,
    status: PropTypes.string,
    createdAt: PropTypes.string,
    updatedAt: PropTypes.string,
    expiresAt: PropTypes.string,
    googlePlacesId: PropTypes.string,
    skills: PropTypes.array
  }),
  organisation: PropTypes.object.isRequired,
  fetchJobOffersBookmarksForCandidate: PropTypes.func.isRequired,
  candidateId: PropTypes.string.isRequired,
  toggleJobOfferBookmark: PropTypes.func.isRequired
}

export default scriptLoader(`https://maps.googleapis.com/maps/api/js?key=${process.env.REACT_APP_GOOGLE_PLACES_JAVASCRIPT_API_KEY}&libraries=places&language=en`)(
  withRouter(withTranslations(withLoader(JobDetailsView, JobDetailsLoader)))
)



