import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { Trans } from 'react-i18next'
import { ConfirmModal } from 'Shared/components/DeleteModal'

import './RejectMessageModal.scss'

class RejectMessageModal extends PureComponent {
  get rejectMessage () {
    return (
      <Trans i18nKey='rejectedCandidateBox.fullMessage' className='profile-reject__full-message'>
        <p>Your profile may have been rejected due to the following reasons:</p>
        <ul>
          <li>Your profile does not meet Oliv's required standards for profile activation.</li>
          <li>You have 2 or more years of work experience.</li>
        </ul>
        <p>We try very hard to make sure our students and new graduates are given a fair chance to prove themselves and launch their careers. This involves several screening processes and verifications.</p>
        <p>We use a maximum of 2 years of experience as a general rule of thumb to be a member of the Oliv community, there are exceptions to the rule in special circumstances. This is so we can create a safe zone for our candidates where they don't feel intimidated to apply alongside more mature and experienced professionals.</p>
        <p>Any sort of workplace experience of over 2 years holds a lot of value on its own and gives you an unfair advantage over the rest of our community and for this reason we cannot approve your profile. We hope that makes sense, please let us know if we can answer any questions or concerns you might have.</p>
        <p>Happy hunting.</p>
      </Trans>
    )
  }

  render () {
    return (
      <ConfirmModal
        content={this.rejectMessage}
        handleConfirm={this.props.closeMessageModal}
        isModalVisible={this.props.isMessageModalOpen}
        hideConfirmModal={this.props.closeMessageModal}
        showCancel={false}
      />
    )
  }
}

RejectMessageModal.propTypes = {
  closeMessageModal: PropTypes.func.isRequired,
  isMessageModalOpen: PropTypes.bool.isRequired
}

export default RejectMessageModal



