import { postRequest } from './commonApi'
import { endPoints } from './endPoints'
import { setLocalData, getLocalData } from 'Shared/helpers/session'

const apiEmailCareerPro = async (data, key, cancelScheduledEmail) => {

  const EMAIL_TEMPLATE = {
    resume_career_pro: 'resume_career_pro',
    has_applied_more_opportunities: 'has_applied_more_opportunities'
  }

  data = {
    email_to: data.email,
    name: data.firstName,
    key: EMAIL_TEMPLATE[key]
  }

  if (!!EMAIL_TEMPLATE[key] === getLocalData(EMAIL_TEMPLATE[key])) {
    console.log('Already sent!')
    return
  }

  const url = cancelScheduledEmail ? endPoints.cancelEmailCareerPro : endPoints.emailCareerPro

  const header = {
    key: 'Star123***'
  }

  try {
    const res = await postRequest(url, data, true, header)
    setLocalData(EMAIL_TEMPLATE[key], true)
    if (res.success) {
      return res.data
    }
    setLocalData(EMAIL_TEMPLATE[key], false)
  } catch (err) {
    console.log('Err', err)
  }
}

export default apiEmailCareerPro
export { apiEmailCareerPro }



