import React from 'react'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations'
import Subtitle from 'Shared/components/Subtitle'
import './ProfileStepSection.scss'

const ProfileStepSection = ({ label, text, optional, t, id }) => {
  const headerParams = {
    id
  }

  return (
    <div className='profile-step-section'>
      <h3 className='profile-step-section__heading' {...headerParams}>{label}</h3>
      <div className='profile-step-section__text'>{text}</div>
      {optional &&
      <Subtitle className='profile-step-section__optional' text={t('common.optional')} />}
    </div>
  )
}
ProfileStepSection.propTypes = {
  id: PropTypes.string,
  t: PropTypes.func.isRequired,
  label: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.func
  ]).isRequired,
  text: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.node,
    PropTypes.func
  ]),
  optional: PropTypes.bool
}

export default withTranslations(ProfileStepSection)

export const UnavailableSection = ({ children, label }) => {
  return (
    <div className='profile-step-section'>
      <h3 className='profile-step-section__heading'>
        {label}
        <i className='ion-md-lock profile-step-section--unavailable-icon' />
      </h3>
      <div className='profile-step-section__text'>
        {children}
      </div>
    </div>
  )
}



