import React, { PureComponent } from 'react'
import { Field } from 'redux-form'
import moment from 'moment'
import withTranslations from 'Shared/hoc/withTranslations'
import { convertDateToMoment } from 'Shared/helpers/date'
import InputDatePicker from 'Shared/components/FormElements/InputDatePicker'

class AvailabilityForm extends PureComponent {
  render () {
    const { t } = this.props

    return (
      <form className='columns'>
        <div className='column col-6 col-xs-12'>
          <Field
            component={InputDatePicker}
            label={t('forms.startDate.label')}
            name='availableFrom'
            icon={'ion-md-clock'}
            floatRight
            minDate={moment().utc()}
            maxDate={moment().utc().add(3, 'months')}
            placeholderText={t('forms.startDate.placeholder')}
            format={value => convertDateToMoment(value)}
            selectsStart
          />
        </div>
        <div className='column col-6 col-xs-12'>
          <Field
            component={InputDatePicker}
            label={t('forms.endDate.label')}
            name='availableTo'
            icon={'ion-md-clock'}
            floatRight
            minDate={moment().utc().add(1, 'days')}
            maxDate={moment().utc().add(3, 'months')}
            placeholderText={t('forms.endDate.placeholder')}
            format={value => convertDateToMoment(value)}
            selectsEnd
          />
        </div>
      </form>
    )
  }
}

export default withTranslations(AvailabilityForm)



