import { reduxForm, isSubmitting, reset, change } from 'redux-form'
import { connect } from 'react-redux'
import validate from 'Candidate/validators/JobOfferApplyFormValidator'
import JobOfferApplyForm from 'Candidate/forms/JobOffer/JobOfferApplyForm'
import { applyForJob, getApplicationIfAppliedIfNeeded, boostApplication, getApplicationIfApplied } from 'Shared/ducks/jobOfferApplications'
import { getCandidateDefaultCvId, getDocumentCvsAsOptions } from 'Shared/selectors/files'
import { getOrganisationName } from 'Shared/selectors/organisations'
import { isCandidateProfileApproved, isCandidateProfileComplete } from 'Shared/selectors/user'
import { isNil, not } from 'ramda'
import { generateCustomCareerUrlWithUtms } from 'Shared/helpers/query'
import { isExternalJobOffer } from 'Shared/selectors/jobOffers'
import { useApiAtsParseCv } from 'Shared/services/api/ats'

const { apiAtsParseCv } = useApiAtsParseCv()

export const formName = 'JobOfferApplyForm'

let Form = reduxForm({
  form: formName,
  onSubmit: async (values, dispatch, ownProps) => {
    const { cvId } = values
    const { jobOffer: { id, customCareerSiteUrl } } = ownProps

    not(isNil(customCareerSiteUrl)) && window.open(generateCustomCareerUrlWithUtms(customCareerSiteUrl), '_blank')

    await apiAtsParseCv(cvId, id)

    await dispatch(applyForJob(ownProps.jobOffer.id, values, !values.shouldBoostApplication))
    const applicationId = await dispatch(getApplicationIfApplied(ownProps.jobOffer.id))

    if (values.shouldBoostApplication) {
      await dispatch(boostApplication(applicationId))
    }

    ownProps.closeOnSubmit()
    return dispatch(reset(formName))
  },
  enableReinitialize: true,
  validate
})(JobOfferApplyForm)

const mapStateToProps = (state, ownProps) => ({
  isFormSubmitting: isSubmitting(formName)(state),
  candidateFiles: getDocumentCvsAsOptions(state),
  initialValues: {
    coverLetterRequired: ownProps.jobOffer.coverLetterRequired,
    cvId: getCandidateDefaultCvId(state),
    shouldBoostApplication: false
  },
  organisationName: getOrganisationName(state, ownProps.jobOffer.organisationId),
  isCandidateProfileComplete: isCandidateProfileComplete(state),
  isCandidateApproved: isCandidateProfileApproved(state),
  isExternalJobOffer: isExternalJobOffer(state, ownProps.jobOffer.id)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  boostAfterApply: () => dispatch(change(formName, 'shouldBoostApplication', true)),
  getApplicationIfApplied: () => dispatch(getApplicationIfAppliedIfNeeded(ownProps.jobOffer.id))
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



