import JobsDetailsViewWeb from './JobDetailsView'
import JobsDetailsViewMobile from './JobDetailsView.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

import React from 'react'

const JobsDetailsView = (props) => props.isMobile ? <JobsDetailsViewMobile {...props} /> : <JobsDetailsViewWeb {...props} />

export default withDeviceWidthDetector(JobsDetailsView)



