import { isNil, ifElse, always } from 'ramda'
import startCase from 'lodash/startCase'
import { isStillAvailable } from 'Shared/helpers/date'

export default values => {
  const errors = {}

  const fields = ['availableFrom', 'availableTo']

  const isNotEmpty = fieldName => (
    ifElse(
      isNil,
      always({
        key: 'errors.requiredField',
        options: {
          field: startCase(fieldName)
        }
      }),
      always(false)
    )(values[fieldName])
  )

  const isGraterThanToday = fieldName => (
    ifElse(
      isStillAvailable,
      always(false),
      always({
        key: 'errors.dateLater',
        options: {
          field: startCase(fieldName)
        }
      })
    )(values[fieldName])
  )

  fields.forEach(field => {
    errors[field] = isNotEmpty(field)
    errors[field] = isGraterThanToday(field)
  })

  return errors
}



