import React from 'react'
import MessageToEmployerButton from './MessageToEmployerButton'
import MessageToEmployerButtonMobile from './MessageToEmployerButton.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const MessageToEmployerButtonComponent = props => props.isMobile ? <MessageToEmployerButtonMobile {...props} /> : <MessageToEmployerButton {...props} />

export default withDeviceWidthDetector(MessageToEmployerButtonComponent)



