import React, { Component } from 'react'
import { Field, Form } from 'redux-form'
import { lengthLimit } from 'Shared/constants/Editor'
import withTranslations from 'Shared/hoc/withTranslations'
import Editor from 'Shared/components/FormElements/Editor/Editor'

class MessageEmployerForm extends Component {
  render () {
    const { t, handleSubmit } = this.props

    return (
      <div>
        <div className='form__group'>
          <Form onSubmit={handleSubmit}>
            <Field
              component={Editor}
              name='messageToEmployer'
              customMode={{
                hideToolbar: true
              }}
              limit={lengthLimit.messageToEmployer}
              label={t('olivPlus.employerMessageForm.label')}
              placeholder={t('olivPlus.employerMessageForm.placeholder')}
              preventLengthCutting
            />
          </Form>
        </div>
      </div>
    )
  }
}

export default withTranslations(MessageEmployerForm)



