import React from 'react'
import OlivPlusSmartPopUp from 'Candidate/components/OlivPlusSmartPopUp/OlivPlusSmartPopUp'
import OlivPlusSmartPopUpMobile from 'Candidate/components/OlivPlusSmartPopUp/OlivPlusSmartPopUp.mobile'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const OlivPlusSmartPopUpElement = props => props.isMobile ? <OlivPlusSmartPopUpMobile {...props} /> : <OlivPlusSmartPopUp {...props} />

export default withDeviceWidthDetector(OlivPlusSmartPopUpElement)



