import { connect } from 'react-redux'
import CompanyDetailsSidebar from 'Shared/components/CompanyDetailsSidebar/CompanyDetailsSidebar'
import { getJobOfferById } from 'Shared/selectors/jobOffers'
import { getOrganisation, getOrganisationIndustryNameById } from 'Shared/selectors/organisations'
import { getCompanySizesEntitiesByValue } from 'Shared/selectors/resources'
import { getAllOrganisationImages } from 'Shared/selectors/files'

const mapStateToProps = (state, ownProps) => {
  const jobOffer = getJobOfferById(state, ownProps.jobOfferId)

  return ({
    organisation: getOrganisation(state, jobOffer.organisationId),
    industryName: getOrganisationIndustryNameById(state, jobOffer.organisationId),
    companySize: getCompanySizesEntitiesByValue(state),
    companyImages: getAllOrganisationImages(state, jobOffer.organisationId)
  })
}

export default connect(mapStateToProps)(CompanyDetailsSidebar)



