import { connect } from 'react-redux'
import OlivPlusSmartPopUp from 'Candidate/components/OlivPlusSmartPopUp'
import { isCandidateProfileApproved } from 'Shared/selectors/user'
import {
  getApplicantBoostAmountLeft,
  getApplicantBoostMaxAmount, getEmployerMessageAmountLeft, getEmployerMessageMaxAmount, getOlivPlusExpirationDate,
  hasOlivPlusMembership, hasAnyBoostsLeft, hasAnyMessagesLeft
} from 'Shared/selectors/membership'
import { getPriceWithCurrency } from 'Shared/helpers/payment'
import { getOlivPlusBasePrice, getPaymentCurrency } from 'Shared/selectors/geolocation'
import { boostApplication } from 'Shared/ducks/jobOfferApplications'
import { getApplicationById, isApplicationBoosted } from 'Shared/selectors/jobOfferApplications'
import { submit, hasSubmitSucceeded, isSubmitting } from 'redux-form'
import { formName } from 'Candidate/connectors/Forms/MessageEmployerFormConnector/MessageEmployerFormConnector'

const mapStateToProps = (state, ownProps) => {
  return {
    hasMembership: hasOlivPlusMembership(state),
    hasAnyBoostsLeft: hasAnyBoostsLeft(state),
    hasAnyMessagesLeft: hasAnyMessagesLeft(state),
    isApproved: isCandidateProfileApproved(state),
    applicantBoostsMaxAmount: getApplicantBoostMaxAmount(state),
    applicantBoostsAmountLeft: getApplicantBoostAmountLeft(state),
    employerMessageAmountLeft: getEmployerMessageAmountLeft(state),
    employerMessageMaxAmount: getEmployerMessageMaxAmount(state),
    membershipRenewalDate: getOlivPlusExpirationDate(state),
    formattedPrice: getPriceWithCurrency(getOlivPlusBasePrice(state), getPaymentCurrency(state)),
    application: getApplicationById(state, ownProps.actionId),
    isApplicationBoosted: isApplicationBoosted(state, ownProps.actionId),
    hasMessageEmployerSubmitSucceeded: hasSubmitSucceeded(formName)(state),
    isFormSubmitting: isSubmitting(formName)(state)
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  const submitAction = async (id) => {
    switch (ownProps.service) {
      case 'boost':
        return ownProps.customSubmit ? ownProps.customSubmit() : dispatch(boostApplication(id))
      case 'message':
        return {}
      default:
        return ownProps.history.push('/candidate/oliv-plus/review')
    }
  }

  return {
    onSubmit: actionId => submitAction(actionId),
    messageEmployer: () => dispatch(submit(formName))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(OlivPlusSmartPopUp)



