import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import './AlertBox.scss'

const AlertBox = ({ title, text, variant, iconClassName, alertBoxClassName }) => {
  const alertBoxClass = cx({
    'c-alert-box': true,
    [`c-alert-box--${variant}`]: variant,
    [alertBoxClassName]: !!alertBoxClassName
  })
  const iconClass = cx({
    'c-alert-box__icon': true,
    [iconClassName]: !!iconClassName
  })
  return (
    <div className={alertBoxClass}>
      <i className={iconClass} />
      <div className='c-alert-box__content'>
        <h4 className='c-alert-box__heading'>{title}</h4>
        <p className='c-alert-box__text'>{text}</p>
      </div>
    </div>
  )
}

AlertBox.propTypes = {
  text: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  alertBoxClassName: PropTypes.string,
  iconClassName: PropTypes.string,
  variant: PropTypes.oneOf([
    'info',
    'warning',
    'danger',
    'success'
  ]).isRequired
}

AlertBox.defaultProps = {
  variant: 'info',
  iconClassName: 'icon-im-info-outline'
}

export default AlertBox



