import { getRequest } from '../commonApi'
import { endPoints } from '../endPoints'


const useApiGetAtsMatchScore = () => {
  const apiGetAtsMatchScore = async (jobId, candidateIds = []) => {

    const token = JSON.parse(localStorage.getItem('olive-app-token'))
    const url = endPoints.getAtsMatchScore + '?access_token=' + token + '&jobId=' + jobId + '&candidateIds=' + candidateIds
    try {
      let res = await getRequest(url)
      if (res) {
        return res
      }
    } catch (err) {
      console.log('Err', err)
    }
  }
  return { apiGetAtsMatchScore }
}

export default useApiGetAtsMatchScore




