import { connect } from 'react-redux'
import { submit, hasSubmitSucceeded } from 'redux-form'
import { formName } from 'Candidate/connectors/Forms/Availability/Availability'
import AvailabilityModal from 'Candidate/components/AvailabilityModal'
import { isAvailable } from 'Shared/selectors/user'

const mapStateToProps = state => ({
  hasSubmitSucceeded: hasSubmitSucceeded(formName)(state),
  isAvailable: isAvailable(state)
})

const mapDispatchToProps = dispatch => ({
  submitForm: () => dispatch(submit(formName))
})

export default connect(mapStateToProps, mapDispatchToProps)(AvailabilityModal)



