// TODO: Extract to separate lib and release on npm.
import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Mousetrap from 'mousetrap'
import cx from 'classnames'
import withTranslations from 'Shared/hoc/withTranslations'
import './Drawer.scss'
import { isMobile } from 'Shared/helpers/mobile'
import { isArabicLayout } from 'Shared/helpers/layout'

class Drawer extends Component {
  static propTypes = {
    isOpen: PropTypes.bool,
    t: PropTypes.func,
    title: PropTypes.string,
    overlayClassName: PropTypes.string,
    children: PropTypes.node.isRequired,
    drawerContainerClassName: PropTypes.string
  }

  static defaultProps = {
    onClose: () => {}
  }

  state = {
    isOpen: false
  }

  componentWillMount = () => {
    Mousetrap.bind('esc', this.hideDrawer)
  }

  componentDidMount = () => {
    this.setState({
      isOpen: this.props.isOpen
    })
  }

  componentWillReceiveProps (nextProps) {
    if (nextProps.isOpen !== this.state.isOpen) {
      this.handleBodyScroll(nextProps.isOpen)

      return nextProps.isOpen
        ? this.showDrawer()
        : this.hideDrawer()
    }
  }

  handleBodyScroll = isOpen => {
    if (!isMobile()) {
      return
    }

    if (isOpen) {
      document.documentElement.classList.add('u-no-flow')
      document.body.classList.add('u-no-flow')
    } else {
      document.documentElement.classList.remove('u-no-flow')
      document.body.classList.remove('u-no-flow')
    }
  }

  componentWillUnmount = () => {
    Mousetrap.unbind('esc', this.hideDrawer)
    document.documentElement.classList.remove('u-no-flow')
    document.body.classList.remove('u-no-flow')
  }

  hideDrawer = () => {
    const { onClose } = this.props
    this.setState({
      isOpen: false
    })

    return onClose()
  }

  showDrawer = () => {
    this.setState({
      isOpen: true
    })
  }

  renderOverlay = () => {
    const { drawerContainerClassName } = this.props
    const { isOpen } = this.state
    const overlayClass = cx({
      'c-drawer__overlay': true,
      'c-drawer__overlay--visible': isOpen,
      [drawerContainerClassName]: !!drawerContainerClassName
    })

    return <div onClick={this.hideDrawer} className={overlayClass} />
  }

  render () {
    const { drawerContainerClassName, t } = this.props
    const { isOpen } = this.state

    const drawerContainerClass = cx({
      'c-drawer__container': true,
      'c-drawer__container--visible': isOpen,
      'c-drawer__container-arabic': isArabicLayout,
      [drawerContainerClassName]: !!drawerContainerClassName
    })

    return (
      <div>
        {this.renderOverlay()}
        <div className={drawerContainerClass}>
          <button onClick={this.hideDrawer} className='ion-md-close c-drawer__close' />
          <h3 className='c-drawer__title'>{this.props.title || t('jobOffer.jobDetailsDrawer.applyForAJob')}</h3>
          <div className='c-drawer__content'>
            {this.props.children}
          </div>
        </div>
      </div>
    )
  }
}

export default withTranslations(Drawer)



