import { complement, isEmpty, isNil } from 'ramda'

export const isUndefined = val => typeof val === 'undefined'

export const exists = val => !isUndefined(val) && !isNil(val)

export const renderable = val => !isEmpty(val) && exists(val)

export const listRenderable = list => !list.every(complement(renderable))



