import React, { Component } from 'react'
import { t } from 'i18next'
import { isNil, isEmpty } from 'ramda'
import Loader from 'Shared/components/Loader/Loader'

const withLoader = (ComposedComponent, LoadingComponent) => class HOC extends Component {
  state = {
    isLoading: true,
    receiveFetchedData: false
  }

  finishLoading = (data, callback) => this.setState({ isLoading: false, receiveFetchedData: isNil(data) || isEmpty(data) }, () => callback && callback())

  render () {
    switch (true) {
      case this.state.isLoading:
        return (
          <div>
            {LoadingComponent ? <LoadingComponent /> : <Loader className='loader--app' description={`${t('common.loading')}...`} />}
            <ComposedComponent finishLoading={this.finishLoading} {...this.props} />
          </div>
        )
      case this.state.receiveFetchedData && !this.state.isLoading:
        window.location.href = '/not-found'
        return LoadingComponent ? <LoadingComponent /> : <Loader className='loader--app' description={`${t('common.loading')}...`} />
      default:
        return <ComposedComponent finishLoading={this.finishLoading} {...this.props} />
    }
  }
}

export default withLoader



