import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import Modal from 'Shared/components/Modal'
import withTranslations from 'Shared/hoc/withTranslations'
import AvailabilityFormConnector from 'Candidate/connectors/Forms/Availability/Availability'
import NotificationService from 'Shared/services/NotificationService'

import './AvailabilityModal.scss'

class AvailabilityModal extends PureComponent {
  modal = {}

  componentDidUpdate (prevProps) {
    if (this.props.hasSubmitSucceeded && prevProps.hasSubmitSucceeded !== this.props.hasSubmitSucceeded) {
      NotificationService.success(this.props.t('alerts.availabilityUpdated'))
      this.props.onClose()
    }
  }

  handleSubmit = () => {
    this.props.submitForm()
  }

  render () {
    const { t } = this.props

    return (
      <Modal
        ref={el => { this.modal = el }}
        title={this.props.isAvailable ? t('modals.updateAvailability.editTitle') : t('modals.updateAvailability.title')}
        handleClose={this.props.onClose}
        handleCancel={this.props.onClose}
        show={this.props.show}
        handleSubmit={this.handleSubmit}
        cancelText={t('common.cancel')}
        submitText={t('common.update')}
        showContact={false}
        showCancel
        closeOnSubmit={false}
      >
        <div className='availability-modal__text-container'>
          {
            !this.props.isAvailable && <div className='availability-modal__text'>{t('modals.updateAvailability.content')}</div>
          }
          <AvailabilityFormConnector />
        </div>
      </Modal>
    )
  }
}

AvailabilityModal.defaultProps = {
  onClose: () => {}
}

AvailabilityModal.propTypes = {
  show: PropTypes.bool.isRequired,
  hasSubmitSucceeded: PropTypes.bool.isRequired,
  submitForm: PropTypes.func.isRequired,
  onClose: PropTypes.func,
  isFormValid: PropTypes.bool
}

export default withTranslations(AvailabilityModal)



