import React from 'react'
import cx from 'classnames'
import './MessageToEmployerButton.scss'

const MessageToEmployerButtonMobile = ({ onClick, isDisabled, className }) => {
  const buttonClass = cx({
    'button message-to-employer-mobile-button': true,
    [className]: !!className
  })
  return (
    <button className={buttonClass}>
      <i className={'icon-im-chat-conversations'} disabled={isDisabled} onClick={onClick} />
    </button>
  )
}

export default MessageToEmployerButtonMobile



