import { postRequest } from '../commonApi'
import { endPoints } from '../endPoints'

const useApiAtsParseJd = () => {
  const apiAtsParseJd = async (formValuesBase64, jobId) => {
    const token = JSON.parse(localStorage.getItem('olive-app-token'))
    const url = endPoints.atsParseJd + '?access_token=' + token

    const data = {
      jdBase64: formValuesBase64,
      jobId: jobId
    }

    try {
      let res = await postRequest(url, data)
      if (res.success) {
        return res.data
      }
    } catch (err) {
      console.log('Err', err)
    }
  }
 
  return { apiAtsParseJd }
}

export default useApiAtsParseJd



