import React, { useState, Fragment, useEffect } from 'react'
import withTranslations from 'Shared/hoc/withTranslations'
import { withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import cx from 'classnames'
import ModalMobile from 'Shared/components/Modal/Modal.mobile'
import moment from 'moment'
import { Trans } from 'react-i18next'
import MessageEmployerFormConnector
  from 'Candidate/connectors/Forms/MessageEmployerFormConnector/MessageEmployerFormConnector'
import FormModalMobile from 'Shared/components/Modal/FormModal.mobile'
import Notification from 'Shared/components/Notification'
import './OlivPlusSmartPopUp.scss'
import * as R from 'ramda'

const OlivPlusSmartPopUpMobile = props => {
  const {
    t,
    hasMembership,
    history,
    formattedPrice,
    employerMessageAmountLeft,
    employerMessageMaxAmount,
    applicantBoostsMaxAmount,
    applicantBoostsAmountLeft,
    type,
    service,
    onSubmit,
    actionId,
    membershipRenewalDate,
    isApplicationBoosted,
    application,
    messageEmployer,
    jobOfferIdProps,
    isExternalJobOffer,
    hasMessageEmployerSubmitSucceeded,
    isFormSubmitting,
    hasAnyBoostsLeft,
    hasAnyMessagesLeft
  } = props

  const isAllowedToBoost = hasAnyBoostsLeft || hasMembership
  const isAllowedToMessageEmployer = hasAnyMessagesLeft || hasMembership

  useEffect(() => {
    if (!hasMessageEmployerSubmitSucceeded && isVisible) {
      setVisibility(false)
    }
  }, [hasMessageEmployerSubmitSucceeded])

  const renderMessagesSetup = () => {
    const remainingClass = cx({
      'oliv-plus__smart-pop-up--remainingService': true,
      'error-color': employerMessageAmountLeft === 0
    })

    const renderDescription = () => {
      if (application && application.status === 'declined') {
        return <div className='oliv-plus__smart-pop-up--description'>
          <p className={'oliv-plus__smart-pop-up--remainingService'}>{t('olivPlus.smartPopUp.messageDeclined')}</p>
        </div>
      } else {
        return <div className='oliv-plus__smart-pop-up--description'>
          <p className={remainingClass}>{t('olivPlus.smartPopUp.messagesLeft', {
            count: employerMessageAmountLeft,
            total: employerMessageMaxAmount
          })}</p>
        </div>
      }
    }

    return (
      <div>
        <p className='oliv-plus__smart-pop-up--header'>{t('common.messageEmployer')}</p>
        {renderDescription()}
      </div>
    )
  }

  const renderBoostsSetup = () => {
    const remainingClass = cx({
      'oliv-plus__smart-pop-up--remainingService': true,
      'error-color': applicantBoostsAmountLeft === 0
    })

    const renderDescription = () => {
      if (application && application.status === 'declined' && !isApplicationBoosted) {
        return <p className={'oliv-plus__smart-pop-up--remainingService'}>{t('olivPlus.smartPopUp.applicationDeclined')}</p>
      }

      if (type === 'cta') {
        return isApplicationBoosted
          ? <p className={'oliv-plus__smart-pop-up--remainingService'}>{t('olivPlus.smartPopUp.alreadyBoosted')}</p>
          : <p className={remainingClass}>{t('olivPlus.smartPopUp.boostsLeft', {
            count: applicantBoostsAmountLeft,
            total: applicantBoostsMaxAmount
          })}</p>
      } else {
        return <div>
          {applicantBoostsAmountLeft !== 0
            ? <Fragment>
              <p
                className={remainingClass}>{t('olivPlus.smartPopUp.confirmBoost.remainingBoosts', { count: applicantBoostsAmountLeft })}</p>
              {t('olivPlus.smartPopUp.confirmBoost.description', { count: applicantBoostsAmountLeft })}
            </Fragment>
            : <Trans i18nkey='olivPlus.smartPopUp.confirmBoost.outOfBoosts' formattedDate={moment(membershipRenewalDate).format('DD-MM-YYYY')}>
              You’ve run out of profile boosts for this month. But don't worry, you'll receive additional boosts when your Oliv Plus renews on:
              <p>{moment(membershipRenewalDate).format('DD-MM-YYYY')}</p>
            </Trans>
          }
        </div>
      }
    }

    return (
      <div>
        <p className='oliv-plus__smart-pop-up--header'>{t('olivPlus.smartPopUp.boostMyApplication')}</p>
        <div className='oliv-plus__smart-pop-up--description'>
          {renderDescription()}
        </div>
      </div>
    )
  }

  const renderNoMembershipSetup = () => {
    return (
      <div>
        <p className='oliv-plus__smart-pop-up--header'>{t('olivPlus.smartPopUp.standOutToEmployers')}</p>
        <div className='oliv-plus__smart-pop-up--features-list'>
          <div className='oliv-plus__smart-pop-up--features-list-item'>
            <i className='icon-im-done oliv-plus__smart-pop-up--features-list-icon' />
            {t('olivPlus.benefitsSection.boostApplicationsTitle')}
          </div>
          <div className='oliv-plus__smart-pop-up--features-list-item'>
            <i className='icon-im-done oliv-plus__smart-pop-up--features-list-icon' />
            {t('olivPlus.benefitsSection.messageEmployersTitle')}
          </div>
        </div>
      </div>
    )
  }

  const renderExternalJobContent = () => {
    const { service } = props
    return <div>
      <div className='oliv-plus__smart-pop-up--description'>
        {service === 'message' ? t('olivPlus.smartPopUp.externalJobMessageWarning') : t('olivPlus.smartPopUp.externalJobBoostWarning')}
      </div>
    </div>
  }

  const renderButtonText = () => {
    switch (true) {
      case service === 'message' && isAllowedToMessageEmployer && !isExternalJobOffer:
        return t('common.sendMessage')
      case service === 'boost' && isAllowedToBoost && !isExternalJobOffer:
        return type === 'cta'
          ? t('olivPlus.smartPopUp.boostButton')
          : t('common.yes')
      case !hasMembership:
        return t('olivPlus.welcomeSection.getOlivPlus', { formattedPrice })
      default:
        return null
    }
  }

  const renderContent = () => {
    switch (true) {
      case service === 'message' && isAllowedToMessageEmployer && !isExternalJobOffer:
        return renderMessagesSetup()
      case service === 'boost' && isAllowedToBoost && !isExternalJobOffer:
        return renderBoostsSetup()
      case !hasMembership && !isAllowedToMessageEmployer && !isAllowedToBoost:
        return renderNoMembershipSetup()
      case service === 'message' && !hasMembership && !isAllowedToMessageEmployer:
        return renderNoMembershipSetup()
      case service === 'boost' && !hasMembership && !isAllowedToBoost:
        return renderNoMembershipSetup()
      case isExternalJobOffer:
        return renderExternalJobContent()
      default:
        return null
    }
  }

  const redirectToCheckout = () => history.push('/candidate/oliv-plus/review')

  const outOfCredits = service === 'boost' ? applicantBoostsAmountLeft === 0 : employerMessageAmountLeft === 0

  const isBoostConfirmation = type === 'confirm' && service === 'boost' && isAllowedToBoost && applicantBoostsAmountLeft !== 0
  const isNotDeclined = application.status !== 'declined'
  const shouldDisplayButtons = ((type === 'cta' || isBoostConfirmation) && !outOfCredits && isNotDeclined && !isExternalJobOffer) ||
    (!hasMembership && ((service === 'boost' && isAllowedToBoost) || (service === 'message' && isAllowedToMessageEmployer)) && !isExternalJobOffer) ||
    (!hasMembership && ((service === 'boost' && !isAllowedToBoost) || (service === 'message' && !isAllowedToMessageEmployer)))
  const shouldDisplayButtonsWithBoostCondition = service === 'boost' ? shouldDisplayButtons && !isApplicationBoosted : shouldDisplayButtons

  const [isVisible, setVisibility] = useState(false)

  const isButtonActive = service === 'message' ? false : isApplicationBoosted

  const showModal = () => {
    setVisibility(true)
  }

  const hideModal = () => {
    setVisibility(false)
  }

  const buttonClasses = cx({
    'oliv-plus__smart-pop-up--mobile-container': true,
    'oliv-plus__smart-pop-up--mobile-footer-button-full-width': !hasMembership
  })

  const [isModalVisible, setModalVisibility] = useState(false)

  const toggleMessageModal = () => {
    setModalVisibility(!isModalVisible)
  }

  const handleSubmit = () => {
    switch (true) {
      case service === 'message' && isAllowedToMessageEmployer && !isExternalJobOffer:
        return toggleMessageModal()
      case service === 'boost' && isAllowedToBoost && !isExternalJobOffer:
        return onSubmit(actionId)
      case !hasMembership:
        return redirectToCheckout()
      default:
        return null
    }
  }

  const handleEmployerMessage = () => messageEmployer()

  const buttonClassNames = cx({
    [props.buttonClassName]: true,
    'active-button': isButtonActive,
    'disabled-button': isExternalJobOffer
  })

  const applicationId = R.propOr(null, 'id')(application)
  const jobOfferId = jobOfferIdProps || application.jobOfferId

  return (
    <Fragment>
      {React.cloneElement(props.triggerButton, { onClick: showModal, className: buttonClassNames })}
      <ModalMobile
        {...props}
        handleSubmit={handleSubmit}
        className={buttonClasses}
        footerClassName='oliv-plus__smart-pop-up--mobile-footer'
        show={isVisible}
        handleCancel={hideModal}
        handleClose={hideModal}
        submitText={renderButtonText()}
        showCancel={shouldDisplayButtonsWithBoostCondition}
        showSubmit={shouldDisplayButtonsWithBoostCondition}
        cancelText={isBoostConfirmation ? t('common.no') : t('common.cancel')}
      >
        {renderContent()}
      </ModalMobile>
      <FormModalMobile
        title={t('modals.messageEmployer.title')}
        show={isModalVisible}
        handleClose={toggleMessageModal}
        handleSubmit={handleEmployerMessage}
        submitText={isFormSubmitting ? t('common.loading') : t('common.sendMessage')}
        cancelText={t('common.cancel')}
        showSubmit
        showCancel
        disableCancel={isFormSubmitting}
        disableSubmit={isFormSubmitting}
        closeOnSubmit={false}
      >
        <div>
          <div className={'message-employer-modal__description'}>{t('modals.messageEmployer.description')}</div>
          <Notification
            messageText={t('modals.messageEmployer.messagesLeft', {
              count: employerMessageAmountLeft,
              total: employerMessageMaxAmount
            })}
            iconClassName='icon-im-alert-circle-outline'
            className='message-employer-modal__notification'
            warning
          />
          <div className={'message-employer-modal__cta'}>
            <Trans i18nKey='modals.messageEmployer.cta'>
              Not sure what to write? <a href='http://oliv.com/blog' target={'_blank'}>Check out our blog</a> for inspiration and recommendations.
            </Trans>
          </div>
          <MessageEmployerFormConnector applicationId={applicationId} jobOfferId={jobOfferId} />
        </div>
      </FormModalMobile>
    </Fragment>
  )
}

OlivPlusSmartPopUpMobile.propTypes = {
  type: PropTypes.oneOf(['cta', 'confirm']),
  service: PropTypes.oneOf(['boost', 'message'])
}

export default withRouter(withTranslations(OlivPlusSmartPopUpMobile))



