import React, {useState, useEffect} from 'react'
import './CareerproBanner.scss'
import { setLocalData, getLocalData } from 'Shared/helpers/session'
import { withRouter } from 'react-router'
import rightArrow from 'Shared/assets/images/other/rightArrow.svg'
import mixpanel from 'mixpanel-browser'

const CareerproBanner = (props) => {
  const {name, heading, des, image, submitButtonText, arrowSubmitButton, hideCloseButton, className} = props
  const hasBannerModal = getLocalData(name)
  const [isShow, setIsShow] = useState(true)

  const clsoeButtonFun = () => {
    setIsShow(false)
    setLocalData(name, true)
  }

  const handleSubmit = () => {
    const currentURL = props.history.location.pathname;
  
    if (currentURL.includes("/candidate/dashboard")) {
      mixpanel.track("Button Click - Career Pro Banner - Dashboard - Get Started");
    } else if (currentURL.includes("/jobs")) {
      mixpanel.track("Prompt Click - Career Pro - Job Apply");
    }
  
    props.history.push('/career-pro');
  };

  useEffect(() => {
    if (hasBannerModal === true) {
      setIsShow(false)
    } else {
      setIsShow(true)
    }
  }, [])

  return (
    <section className={`${className} ${isShow ? 'careerpro_banner' : 'careerpro_banner_none'}`}>
      <div className='columns'>
        <div className='column col-12 col-md-12'>
          <div className='cols'>
            <div className='content'>
              {heading && <h3>{heading}</h3>}
              {des && <p>{des} { arrowSubmitButton && <img onClick={handleSubmit} src={rightArrow} />}
              </p>}
            </div>
            {image && <div className='image_div'>
              <img src={image} alt='image' />
            </div>}
            {!arrowSubmitButton && <div className='submitButton'>
              <button className='button' onClick={handleSubmit}>{submitButtonText}</button>
            </div>}
            {!hideCloseButton &&
            <div className='closeButton'>
              <svg onClick={clsoeButtonFun} width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'>
                <path d='M19.5509 17.3768L12.1692 9.97974L19.5109 2.62316C20.1096 2.0229 20.1096 1.04987 19.5109 0.449945C18.9119 -0.149982 17.9411 -0.149982 17.3421 0.449945L10.0004 7.80653L2.65909 0.449945C2.06006 -0.149982 1.08932 -0.149982 0.490286 0.449945C-0.108752 1.0502 -0.108752 2.02323 0.490286 2.62316L7.83162 9.97974L0.449279 17.3768C-0.14976 17.977 -0.14976 18.9497 0.449279 19.55C0.748798 19.8501 1.14116 20 1.53385 20C1.92654 20 2.3189 19.8501 2.61841 19.55L10.0004 12.153L17.3821 19.55C17.6816 19.8501 18.074 20 18.4667 20C18.8594 20 19.2517 19.8501 19.5513 19.55C20.1496 18.95 20.1496 17.977 19.5509 17.3768Z' fill='white' />
              </svg>
            </div>}
          </div>
        </div>
      </div>
    </section>
  )
}

export default withRouter(CareerproBanner)


