import { reduxForm } from 'redux-form'
import { connect } from 'react-redux'
import validate from './MessageEmployerFormValidator'
import MessageEmployerForm from 'Candidate/forms/MessageEmployerForm/MessageEmployerForm'
import { userFullName } from 'Shared/selectors/user'
import { messageEmployer } from 'Shared/ducks/jobOfferApplications'

export const formName = 'MessageEmployerForm'

let Form = reduxForm({
  form: formName,
  validate,
  onSubmit: async (values, dispatch, ownProps) => {
    try {
      await dispatch(messageEmployer(ownProps.applicationId, ownProps.jobOfferId, values.messageToEmployer))
      ownProps.closeModal()
    } catch (err) {
      console.warn(err)
    }
  },
  enableReinitialize: true
})(MessageEmployerForm)

const mapStateToProps = state => {
  const fullName = userFullName(state)

  return {
    initialValues: {
      messageToEmployer: `Hello,
        <br /><br /> 
        I'm really interested in this role.
        <br /> <br />
        <span style='background-color: #fff8e5'>
          I believe I'd be a great fit because.. / I'd love to ask a couple of questions about it..
        </span>
        <br /><br />
         I look forward to hearing from you.
        <br /> <br />
        Best regards,
        <br /> ${fullName}`
    }
  }
}

export default connect(mapStateToProps)(Form)



