import { postRequest } from '../commonApi'
import { endPoints } from '../endPoints'

const useApiAtsParseCv = () => {
  const apiAtsParseCv = async (cvId, jobId) => {
    const token = JSON.parse(localStorage.getItem('olive-app-token')) // WIP
    const url = endPoints.atsParseCv + '?access_token=' + token

    const data = {
      cvId: cvId,
      jobId: jobId
    }

    try {
      let res = await postRequest(url, data)
      if (res.success) {
        return res.data
      }
    } catch (err) {
      console.log('Err', err)
    }
  }
  return { apiAtsParseCv }
}



export default useApiAtsParseCv



