import React from 'react'
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types'
import ReadMore from 'Shared/components/ReadMore/ReadMore'
import withTranslations from 'Shared/hoc/withTranslations'
import { COMPANY_SIZE } from 'Shared/constants/Organisation'

import { dasherize } from 'Shared/helpers/string'
import { renderable } from 'Shared/helpers/misc'

import CompanyVideo from './components/CompanyVideo/CompanyVideo'
import CompanyGallery from './components/CompanyGallery/CompanyGallery'
import Divider from 'Shared/components/FormElements/Divider'
import cx from 'classnames'

import { pathOr } from 'ramda'

import './CompanyDetailsSidebar.scss'

const CompanyDetailsSidebar = ({ t, organisation, industryName, handleFollowCompany, companyImages, companySize, lesserMargin }) => {
  const containerClass = cx({
    'company-details-sidebar__container': true,
    'company-details-sidebar__lesser-margin': lesserMargin
  })
  return (
    <aside className={containerClass}>
      <div className='company-details-sidebar__company-info'>
        <Link to={`/companies/${dasherize(organisation.name)}/${organisation.id}`}>
          {organisation.logoUrl &&
          <img className='company-details-sidebar__logo' src={organisation.logoUrl} alt='Company logo' />}
          <div className='company-details-sidebar__company-details'>
            <h3 className='company-details-sidebar__name'>{organisation.name}</h3>
            {
              <p className='company-details-sidebar__details-text'>
                {industryName}
              </p>
            }
            {organisation.size &&
            <p className='company-details-sidebar__details-text'>{pathOr('', [organisation.size, 'name'], companySize)}</p>}
          </div>
        </Link>
      </div>
      {organisation.description &&
      <ReadMore className='company-details-sidebar__description' text={organisation.description} minLength={100} />}
      {organisation.companyPageAvailable && organisation.videoUrl && <CompanyVideo videoUrl={organisation.videoUrl} width='auto' height='auto' /> }
      {organisation.companyPageAvailable && renderable(companyImages) && (
        <div>
          <Divider className='form__divider--sidebar' />
          <CompanyGallery images={companyImages} />
        </div>
      )}

    </aside>
  )
}

CompanyDetailsSidebar.propTypes = {
  data: PropTypes.shape({
    id: PropTypes.string.isRequired,
    logo: PropTypes.string,
    name: PropTypes.string.isRequired,
    size: PropTypes.oneOf(COMPANY_SIZE),
    description: PropTypes.string,
    industryId: PropTypes.string,
    industry: PropTypes.object,
    companyPageAvailable: PropTypes.bool.isRequired
  })
}

export default withTranslations(CompanyDetailsSidebar)



